<template>
  <div class="block mb-4">
    <div v-if="isError && !scan" class="mb-4 p-4">
      <Alert color="red" view="smooth" class="mb-5" dense>
        There seems to be an issue with your Phonecheck account, please contact
        their support team here:
        <a href="https://www.phonecheck.com/" rel="noreferrer noopener">https://www.phonecheck.com/</a>
        <template #left>
          <span class="material-icons text-4xl">info</span>
        </template>
      </Alert>
    </div>
    <div v-if="!scan" class="grid grid-cols-2 gap-4">
      <div>
        <div>
          <label>Item Information:</label>
          <template v-if="!isError">
            <table class="table-fixed w-full mt-1 mb-6">
              <tr v-for="(item, itemIndex) of DeviceInfo" :key="itemIndex">
                <td>{{ item.name }}</td>
                <td>
                  <strong>{{ item.value }}</strong>
                </td>
              </tr>
            </table>
          </template>
          <template v-else>
            <div class="px-1 my-2">
              <p>No data to show</p>
            </div>
          </template>
          <label>Computer/Session Information:</label>
          <template v-if="!isError">
            <table class="table-fixed w-full mt-1">
              <tr v-for="(item, itemIndex) of SessionInfo" :key="itemIndex">
                <td>{{ item.name }}</td>
                <td>
                  <strong>{{ item.value }}</strong>
                </td>
              </tr>
            </table>
          </template>
          <template v-else>
            <div class="px-1 my-2">
              <p>No data to show</p>
            </div>
          </template>
        </div>
      </div>
      <div>
        <label>Item Pass/Fail Condition</label>
        <template v-if="!isError">
          <table class="table-fixed w-full mt-1">
            <tr v-for="(item, itemIndex) of DeviceCondition" :key="itemIndex">
              <td>{{ item.name }}</td>
              <td>
                <span v-if="item.value == 'passed'" class="text-green-03"
                  ><strong
                    ><i class="material-icons text-lg">check</i> Passed</strong
                  ></span
                >
                <span v-else class="text-red-03"
                  ><strong
                    ><i class="material-icons text-lg">close</i> Failed</strong
                  ></span
                >
              </td>
            </tr>
          </table>
        </template>
        <template v-else>
          <div class="px-1 my-2">
            <p>No data to show</p>
          </div>
        </template>
      </div>
    </div>
    <div v-if="scan" class="grid grid-cols-2 gap-4">
      <div>
        <label>Item Information:</label>
        <template v-if="!isError">
          <table class="table-scan w-full mt-1 mb-6">
            <tr v-for="(item, itemIndex) of DeviceInfo" :key="itemIndex">
              <td class="text-grey-04">{{ item.name }}</td>
              <td class="px-1">:</td>
              <td>
                <template v-if="(typeof item.value) === 'object' && item.value">
                  <div v-if="item.value.length">
                    <div
                      v-for="(itemChild, itemChildIndex) in item.value"
                      :key="itemChildIndex">
                      <template v-if="itemChild.value">
                        <span class="text-grey-base text-sm capitalize">
                          {{ itemChild.name }}
                        </span>
                        <strong class="text-grey-base text-sm">
                          ({{ itemChild.value }})
                        </strong>
                      </template>
                    </div>
                  </div>
                </template>
                <template v-else>
                  <strong class="text-grey-base">
                    {{ item.value || '-' }}
                  </strong>
                </template>
              </td>
            </tr>
          </table>
        </template>
        <template v-else>
          <div class="px-1 my-2">
            <p>No data to show</p>
          </div>
        </template>
      </div>
      <div>
        <label>Item Pass/Fail Condition:</label>
        <template v-if="!isError">
          <table class="table-scan w-full mt-1 mb-6">
            <tr v-for="(item, itemIndex) of DeviceCondition" :key="itemIndex">
              <td class="text-grey-04 py-0">{{ item.name }}</td>
              <td class="px-1">:</td>
              <td>
                <span v-if="item.value == 'passed'" class="text-green-03">
                  <strong class="flex items-center gap-x-1">
                    <i class="material-icons text-lg">check</i> Passed
                  </strong>
                </span>
                <span v-else class="text-red-03">
                  <strong class="flex items-center gap-x-1">
                    <i class="material-icons text-lg">close</i> Failed
                  </strong>
                </span>
              </td>
            </tr>
          </table>
        </template>
        <template v-else>
          <div class="px-1 my-2">
            <p>No data to show</p>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, toRef, onMounted } from 'vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import AutoComplete from 'bbUi/components/auto-complete/auto-complete.vue'
import Currency from 'components/Currency.vue'
import Accordion from 'components/Accordion.vue'
import BBTable from 'components/Table/BBTable.vue'

export default defineComponent({
  components: {
    Input,
    Select,
    Button,
    Alert,
    AutoComplete,
    Currency,
    Accordion,
    BBTable
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        info: null
      })
    },
    isError: {
      type: Boolean,
      default: false
    },
    scan: {
      type: Boolean,
      default: false
    }
  },
  emits: [],
  setup(props, { emit }) {
    const Info = toRef(props, 'data')
    const DeviceInfo = ref([])
    const SessionInfo = ref([])
    const DeviceCondition = ref([])
    const Passed = ref([])
    const Failed = ref([])
    const getCondition = (device) => {
      let pass = Passed.value.includes(device)
      let fail = Failed.value.includes(device)
      if (fail) {
        return 'failed'
      } else {
        return 'passed'
      }
    }

    onMounted(() => {
      if (!props.isError) {
        if (!props.scan) {
          Passed.value = Info.value.Passed.split(',')
          Failed.value = Info.value.Failed.split(',')
  
          DeviceInfo.value = [
            { name: 'OS', value: Info.value.OS },
            { name: 'Version', value: Info.value.Version },
            { name: 'Make', value: Info.value.Make },
            { name: 'Model', value: Info.value.Model },
            { name: 'Memory', value: Info.value.Memory },
            { name: 'IMEI', value: Info.value.IMEI },
            { name: 'Carrier', value: Info.value.Carrier },
            { name: 'Serial', value: Info.value.Serial },
            { name: 'DeviceLock', value: Info.value.DeviceLock },
            { name: 'Color', value: Info.value.Color },
            { name: 'Battery Cycle', value: Info.value.BatteryCycle },
            {
              name: 'Battery Health Percentage',
              value: Info.value.BatteryHealthPercentage
            },
            { name: 'ESN', value: Info.value.ESN },
            { name: 'Unlock Status', value: Info.value.UnlockStatus },
            { name: 'MDM Response', value: Info.value.MDM },
            { name: 'Fully-Working', value: Info.value.Working }
          ]
          SessionInfo.value = [
            { name: 'Station ID', value: Info.value.StationID },
            { name: 'StartTime', value: Info.value.StartTime },
            { name: 'EndTime', value: Info.value.EndTime },
            { name: 'TransactionID', value: Info.value.TransactionID },
            { name: 'Master_id', value: Info.value.master_id },
            { name: 'WareHouse', value: Info.value.WareHouse },
            { name: 'MasterName', value: Info.value.MasterName },
            { name: 'TesterName', value: Info.value.TesterName },
            { name: 'BuildNo', value: Info.value.BuildNo },
            { name: 'AppVersion', value: Info.value.AppVersion }
          ]
          DeviceCondition.value = [
            { name: 'Accelerometer', value: getCondition('Accelerometer') },
            { name: 'Bluetooth', value: getCondition('Bluetooth') },
            { name: 'Digitizer', value: getCondition('Digitizer') },
            { name: 'Ear Speaker', value: getCondition('Ear Speaker') },
            { name: 'Flashlight', value: getCondition('Flashlight') },
            { name: 'Flip Switch', value: getCondition('Flip Switch') },
            {
              name: 'Front Camera Quality',
              value: getCondition('Front Camera Quality')
            },
            { name: 'Front Microphone', value: getCondition('Front Microphone') },
            {
              name: 'Front Video Camera',
              value: getCondition('Front Video Camera')
            },
            { name: 'Gyroscope', value: getCondition('Gyroscope') },
            { name: 'Headset Port', value: getCondition('Headset Port') },
            { name: 'Headset Left', value: getCondition('Headset-Left') },
            { name: 'Headset Right', value: getCondition('Headset-Right') },
            { name: 'Home Button', value: getCondition('Home Button') },
            { name: 'LCD', value: getCondition('LCD') },
            { name: 'Loudspeaker', value: getCondition('Loud Speaker') },
            { name: 'Microphone', value: getCondition('Microphone') },
            { name: 'Power Button', value: getCondition('Power Button') },
            { name: 'Proximity Sensor', value: getCondition('Proximity Sensor') },
            { name: 'Screen Rotation', value: getCondition('Screen Rotation') },
            { name: 'Video Microphone', value: getCondition('Video Microphone') },
            { name: 'Volume Down', value: getCondition('Volume Down Button') },
            { name: 'Volume Up', value: getCondition('Volume Up Button') },
            { name: 'Wifi', value: getCondition('Wifi') },
            {
              name: 'Battery/Battery Warning',
              value: getCondition('Battery/Battery Warning')
            },
            {
              name: 'Fingerprint Sensor',
              value: getCondition('Fingerprint Sensor')
            },
            { name: 'Force Touch', value: getCondition('Force Touch') },
            { name: 'Glass Cracked', value: getCondition('Glass Cracked') },
            { name: 'Rear Camera', value: getCondition('Rear Camera') },
            {
              name: 'Rear Camera Quality',
              value: getCondition('Rear Camera Quality')
            },
            {
              name: 'Rear Video Camera',
              value: getCondition('Rear Video Camera')
            },
            { name: 'Vibration', value: getCondition('Vibration') },
            { name: 'Notes', value: getCondition('Notes') },
            { name: 'OEM Parts', value: getCondition('OEM Parts') }
          ]
        } else {
          const keysInfo = Object.keys(Info.value).filter(key => key !== 'Diagnostics' && key !== 'Date')
          const diagnostics = {...Info.value.Diagnostics, ...Info.value.Diagnostics.Detailed}
          const keysDiagnostic = Object.keys(diagnostics).filter(key => key !== 'Detailed')
          DeviceInfo.value = keysInfo.map(key => ({
            name: key,
            value: Info.value[key] && (typeof Info.value[key] === 'object')
              ? Object.keys(Info.value[key]).map(keyValue => ({
                  name: keyValue.replaceAll('_', ' '),
                  value: Info.value[key][keyValue]
                }))
              : Info.value[key]
          }))
          DeviceCondition.value = keysDiagnostic.map(key => ({
            name: key,
            value: ['Perfect', 1, true].includes(diagnostics[key]) ? 'passed' : 'failed'
          }))
        }
      } else {
        Passed.value = []
        Failed.value = []
      }
    })

    return {
      Info,
      DeviceInfo,
      SessionInfo,
      DeviceCondition
    }
  }
})
</script>

<style lang="scss" scoped>
.table-scan {
  td {
    padding-top: .2rem;
    padding-bottom: .2rem;
    border-bottom: 0;
    vertical-align: top;
  }
  &--child {
    td {
      padding-top: 0;
      padding-bottom: 0;
    }
  }
  td:nth-child(2) {
    padding-left: .2rem;
    padding-right: .2rem;
  }
}
</style>